import api from './axios';

// 친구 관련 API
export const friendApi = {

    // 친구 요청 목록 조회
    getFriendRequests: () => api.get('/api/friends/request'),
    
    // 사용자 검색
    searchUsers: (name) => api.get(`/api/users/search?name=${name}`),
    
    // 친구 추가
    addFriend: (userId) => api.post('/api/friends/add', { friend_id: userId }),
    
    // 친구 삭제/요청 취소
    removeFriend: (userId, type) => api.post('/api/friends/remove', { friend_id: userId, type: type }),
    
    // 친구 요청 수락
    acceptRequest: (userId) => api.post('/api/friends/accept', { friend_id: userId }),
    
    // 친구 요청 거절
    rejectRequest: (userId) => api.post('/api/friends/reject', { friend_id: userId }),

    // 친구 목록
    getFriends: () => api.get('api/friends/list'),
};

export default friendApi;