import api from './axios';

export const chatApi = {

    // 메세지 보내기
    sendMsg: (message, room_id, user_arr, action) => api.post(`/api/messages`, {content: message,room_id: room_id, user_arr: user_arr, action: action}),

    // 메세지 목록
    msgList: (room_id) => api.get(`/api/messages?room_id=${room_id}`),
    
    // 채팅방 목록
    chatList: () => api.get(`/api/chat/list`),

    // 개인 채팅방 생성 or 채팅방 id 가져오기
    chatId: (friendId) => api.post(`api/chat/join`, {friend_id: friendId}),

    // 그룹 채팅방 생성
    groupChat: (roomName, friendsId) => api.post('api/chat/groupChat', {room_name : roomName, friends: friendsId}),

    // 채팅방 퇴장
    leaveChat: (room_id, room_type, user_arr, action) => api.post('api/chat/leave', {room_id : room_id, room_type: room_type, user_arr: user_arr, action: action}),

    // 친구 초대
    inviteFriends: (room_id, room_type, friend_arr, user_arr, action) => api.post('api/chat/invite', {
        room_id : room_id,
        room_type: room_type,
        friend_arr: friend_arr,
        user_arr: user_arr,
        action: action
    }),

};

export default chatApi;